<template>
  <div class="min-h-screen bg-white text-blue-700">
    <!-- Barra de Navegação -->
    <nav class="w-full bg-white shadow-md p-4">
      <div class="flex items-center justify-between max-w-7xl mx-auto">
        <!-- Logo -->
        <div class="flex items-center space-x-3">
          <img src="@/assets/logo.png" alt="Logo" class="h-10 w-10" />
          <h1 class="text-2xl font-bold text-blue-700">Busca Oferta</h1>
        </div>
        <ul class="flex space-x-8">
          <li>
            <router-link to="/promocoes" class="hover:underline text-blue-700 font-semibold">Promoções</router-link>
          </li>
          <li>
            <router-link to="/" class="hover:underline text-blue-700 font-semibold">Buscar</router-link>
          </li>
          <li>
            <router-link to="/login" class="hover:underline text-blue-700 font-semibold">Login</router-link>
          </li>
        </ul>
      </div>
    </nav>

    <!-- Formulário de Login -->
    <div class="max-w-md mx-auto p-6 bg-white rounded-lg shadow mt-10">
      <h2 class="text-2xl font-bold mb-6">Login para Empresas</h2>
      <form @submit.prevent="login">
        <div class="mb-4">
          <label for="username" class="block text-gray-700">Nome da Empresa</label>
          <input
            v-model="username"
            type="text"
            id="username"
            class="mt-1 block w-full border-gray-300 rounded-md"
            required
          />
        </div>
        <div class="mb-4">
          <label for="password" class="block text-gray-700">Senha</label>
          <input
            v-model="password"
            type="password"
            id="password"
            class="mt-1 block w-full border-gray-300 rounded-md"
            required
          />
        </div>
        <button type="submit" class="w-full bg-blue-500 text-white py-2 rounded-lg">
          Login
        </button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    login() {
      if (this.username && this.password) {
        localStorage.setItem("empresaLogged", "true");
        this.$router.push({ name: "dashboard" });
      } else {
        alert("Por favor, preencha todos os campos.");
      }
    },
  },
};
</script>

  