<template>
  <div class="min-h-screen bg-white text-blue-700">
    <!-- Barra de Navegação -->
    <nav class="w-full bg-white shadow-md p-4">
      <div class="flex items-center justify-between max-w-7xl mx-auto">
        <!-- Logo -->
        <div class="flex items-center space-x-3">
          <img src="@/assets/logo.png" alt="Logo" class="h-10 w-10" />
          <h1 class="text-2xl font-bold text-blue-700">Busca Oferta</h1>
        </div>
        <ul class="flex space-x-8">
          <li>
            <router-link to="/promocoes" class="hover:underline text-blue-700 font-semibold">Promoções</router-link>
          </li>
          <li>
            <router-link to="/" class="hover:underline text-blue-700 font-semibold">Buscar</router-link>
          </li>
          <li>
            <router-link to="/login" class="hover:underline text-blue-700 font-semibold">Login</router-link>
          </li>
        </ul>
      </div>
    </nav>

    <div v-if="isAuthenticated" class="p-6 max-w-4xl mx-auto">
      <h2 class="text-2xl font-bold mb-4">Gerenciar Promoções</h2>

      <!-- Formulário para Criar Promoção -->
      <form @submit.prevent="criarPromocao" class="mb-6 p-4 bg-white rounded-lg shadow-lg">
        <div class="mb-4">
          <label class="block text-gray-700 font-semibold">Título</label>
          <input v-model="novaPromocao.titulo" type="text" class="w-full border-gray-300 rounded-md p-2" required />
        </div>
        <div class="mb-4">
          <label class="block text-gray-700 font-semibold">Descrição</label>
          <textarea v-model="novaPromocao.descricao" class="w-full border-gray-300 rounded-md p-2" required></textarea>
        </div>
        <div class="mb-4">
          <label class="block text-gray-700 font-semibold">Imagem</label>
          <input type="file" @change="onFileChange" class="w-full" required />
        </div>
        <button type="submit" class="w-full bg-green-500 text-white py-2 rounded-lg">Criar Promoção</button>
      </form>

      <!-- Listagem de Promoções -->
      <div v-for="promocao in promocoes" :key="promocao.id" class="mb-6 p-6 bg-gray-100 rounded-lg shadow-lg">
        <h3 class="text-xl font-semibold text-blue-700">{{ promocao.titulo }}</h3>
        <p class="text-gray-700 mb-2">{{ promocao.descricao }}</p>
        <img :src="promocao.imagem" alt="Promoção imagem" class="mt-2 w-full h-48 object-cover rounded-lg" />
        <div class="flex space-x-2 mt-4 justify-center">
          <button @click="atualizarPromocao(promocao)" class="bg-yellow-500 text-white px-4 py-1 rounded">Editar</button>
          <button @click="deletarPromocao(promocao.id)" class="bg-red-500 text-white px-4 py-1 rounded">Deletar</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PromoService from "@/services/PromoService";

export default {
  data() {
    return {
      novaPromocao: { titulo: "", descricao: "", imagem: null },
      promocoes: [],
      isAuthenticated: localStorage.getItem("empresaLogged") === "true",
    };
  },
  methods: {
    async criarPromocao() {
      try {
        await PromoService.criarPromocao(this.novaPromocao);
        this.promocoes = await PromoService.listarPromocoes();
        this.novaPromocao = { titulo: "", descricao: "", imagem: null }; // Limpa o formulário após a criação
      } catch (error) {
        console.error("Erro ao criar promoção:", error);
      }
    },
    onFileChange(e) {
      this.novaPromocao.imagem = e.target.files[0];
    },
    async atualizarPromocao(promocao) {
      try {
        const updatedPromo = { ...promocao, titulo: "Novo Título", descricao: "Nova Descrição" };
        await PromoService.atualizarPromocao(updatedPromo.id, updatedPromo);
        this.promocoes = await PromoService.listarPromocoes();
      } catch (error) {
        console.error("Erro ao atualizar promoção:", error);
      }
    },
    async deletarPromocao(id) {
      try {
        await PromoService.deletarPromocao(id);
        this.promocoes = await PromoService.listarPromocoes();
      } catch (error) {
        console.error("Erro ao deletar promoção:", error);
      }
    },
  },
  async created() {
    try {
      this.promocoes = await PromoService.listarPromocoes();
    } catch (error) {
      console.error("Erro ao carregar promoções:", error);
    }
  },
};
</script>

<style scoped>
/* Estilos adicionais para melhorar a experiência do usuário */
.promo-card {
  transition: transform 0.2s;
}
.promo-card:hover {
  transform: scale(1.02);
}
</style>

