// PromoService.js
import axios from 'axios';

const API_URL = 'https://fabiosantos-buscaofertaapi.hf.space';

export default {
  listarPromocoes() {
    return axios.get(`${API_URL}/empresas/1/promocoes/`).then(response => response.data);
  },
  criarPromocao(promocao) {
    const formData = new FormData();
    formData.append('titulo', promocao.titulo);
    formData.append('descricao', promocao.descricao);
    formData.append('imagem', promocao.imagem);
    return axios.post(`${API_URL}/empresas/1/promocoes/`, formData);
  },
  deletarPromocao(id) {
    return axios.delete(`${API_URL}/empresas/1/promocoes/${id}`);
  },
  // Nova função para buscar promoções
  buscarPromocoes(query) {
    return axios.get(`${API_URL}/promocoes/search/`, {
      params: { q: query }
    }).then(response => response.data);
  },
};

