<template>
    <div class="min-h-screen bg-white text-blue-700">
    <!-- Barra de Navegação -->
    <nav class="w-full bg-white shadow-md p-4">
      <div class="flex items-center justify-between max-w-7xl mx-auto">
        <!-- Logo -->
        <div class="flex items-center space-x-3">
          <img src="@/assets/logo.png" alt="Logo" class="h-10 w-10" />
          <h1 class="text-2xl font-bold text-blue-700">Busca Oferta</h1>
        </div>
        <ul class="flex space-x-8">
          <li>
            <router-link to="/promocoes" class="hover:underline text-blue-700 font-semibold">Promoções</router-link>
          </li>
          <li>
            <router-link to="/" class="hover:underline text-blue-700 font-semibold">Buscar</router-link>
          </li>
          <li>
            <router-link to="/login" class="hover:underline text-blue-700 font-semibold">Login</router-link>
          </li>
        </ul>
      </div>
    </nav>
  
      <!-- Conteúdo Principal -->
      <div class="flex flex-col items-center justify-center text-center mt-16 px-4">
        <h2 class="text-5xl font-extrabold mb-4">Descubra Ofertas Incríveis</h2>
        <p class="text-black text-lg mb-8">Encontre as melhores ofertas de vendedores confiáveis em um só lugar</p>
        
        <!-- Barra de Pesquisa -->
        <div class="flex items-center bg-gray-200 rounded-full shadow-md overflow-hidden max-w-lg w-full mb-8">
          <input
            type="text"
            placeholder="Busque por produtos, serviços ou marcas..."
            class="w-full px-6 py-3 text-blue-700 focus:outline-none"
            v-model="searchQuery"
            @keyup.enter="buscar"
          />
          <button @click="buscar" class="bg-blue-500 hover:bg-blue-600 text-white font-semibold px-6 py-3">
            Buscar
          </button>
        </div>
  
        <!-- Resultados da Busca -->
        <div v-if="promocoes.length > 0" class="w-full max-w-3xl mt-6">
          <h3 class="text-2xl font-bold mb-4">Resultados da Busca</h3>
          <div v-for="promocao in promocoes" :key="promocao.id" class="mb-4 p-4 bg-gray-100 rounded-lg">
            <h4 class="text-xl font-semibold text-blue-700">{{ promocao.titulo }}</h4>
            <p class="text-gray-700">{{ promocao.descricao }}</p>
            <img :src="promocao.imagem" alt="Imagem da promoção" class="mt-2 w-full h-64 object-cover rounded-lg" />
          </div>
        </div>
        <div v-else-if="searched" class="text-gray-500 mt-6">
          Nenhuma promoção encontrada.
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import PromoService from "@/services/PromoService";
  
  export default {
    data() {
      return {
        searchQuery: "",
        promocoes: [],
        searched: false,
      };
    },
    methods: {
      async buscar() {
        if (this.searchQuery) {
          this.promocoes = await PromoService.buscarPromocoes(this.searchQuery);
          this.searched = true;
        }
      },
    },
  };
  </script>
  
  <style>
  /* Fontes e ícones opcionais */
  @import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.4/css/all.min.css");
  </style>
  
  